.go_reg a { color: #a70413; }

.no_acc { font-size: 13px; font-weight: normal; margin-top: 5px; }

.no_acc a { color: rgba(49, 49, 49, 0.61); }

.status_prev { margin-bottom: 30px; }

.status_prev i { font-size: 93px; color: #d10146; }

.or_txt { color: #7a7777; overflow: hidden; }

.or_txt span { position: relative; display: inline-block; padding: 0 30px; background: #fff; z-index: 2; }

.or_txt span:after, .or_txt span:before { content: ''; height: 1px; width: 9999px; position: absolute; top: 50%; left: 100%; display: block; background: #a6a6a6; transform: translateY(-50%); z-index: 1; }

.or_txt span:before { content: ''; left: auto; right: 100%; }

.help_txt p { color: rgba(122, 119, 119, 0.55); font-size: 13px; line-height: 15px; }

.profile_sidebar { flex: 0 0 265px; max-width: 265px; }

.grayscale.grayscale-replaced > svg { display: block !important; max-width: 100% !important; height: auto !important; }

.grayscale.grayscale-replaced { background: none !important; width: auto !important; display: block !important; height: 100px !important; }

.latest_news img { border-radius: 9px; }

.latest_news h2 { margin: 0; font-size: 19px; line-height: 20px; color: #313131; font-weight: bold; padding-bottom: 17px; }

.latest_news p { margin: 9px 0 0 0; font-size: 13px; line-height: 19px; color: #464646; }

.latest_news h3, .latest_news p { padding: 0 10px; }

.latest_news h3 { margin: 0; padding-top: 13px; font-size: 15px; line-height: 17px; font-weight: bold; color: #000; }

.latest_news h3 a { color: #000; text-decoration: none; }

.latest_news h3 a:hover { text-decoration: underline; }

.table_calc { table-layout: fixed; }

.table_calc th { width: 25%; text-align: center; font-size: 19px; line-height: 20px; color: #000; font-weight: normal; border-top: 0; border-bottom: 2px solid #d5d9fa; vertical-align: top !important; padding-top: 17px; padding-bottom: 6px; }

@media (max-width: 1199.98px) { .table_calc th { font-size: 15px; line-height: 16px; } }

.table_calc th:first-child { text-align: left; }

.table_calc th i { font-size: 22px; color: #919292; margin-right: 16px; }

@media (max-width: 1199.98px) { .table_calc th i { font-size: 15px; } }

.table_calc th span { display: block; font-size: 13px; line-height: 14px; color: #b3b2b2; }

.table_calc td { vertical-align: middle !important; text-align: center; font-size: 19px; line-height: 20px; border-bottom: 1px solid #dadada; padding-top: 11px; padding-bottom: 7px; }

@media (max-width: 1199.98px) { .table_calc td { font-size: 18px; line-height: 19px; } }

.table_calc td span { font-weight: 500; }

.table_calc td:first-child { text-align: left; }

.table_calc tr:last-child td { border-bottom: 0; }

.table_calc .bg_gray_2 { background: #f8f8f8; }

.table_calc .group_calc { max-width: 133px; margin: 0 auto; }

.table_total { padding-top: 15px; padding-bottom: 11px; background: linear-gradient(to right, #d9d3f9 0%, #d8d5f9 22%, #cde4fd 78%, #cce6fd 100%); }

.table_total strong, .table_total span { font-size: 19px; line-height: 20px; }

.table_total strong { display: inline-block; vertical-align: middle; color: #d10146; font-weight: bold; min-width: 25%; text-align: center; padding-left: 5px; padding-right: 5px; }

.table_total span { display: inline-block; vertical-align: middle; color: #000; font-weight: normal; }

.user_title .u_t_img { flex: 0 0 70px; max-width: 70px; }

.user_title .u_t_img img { border-radius: 50%; }

.user_title .u_t_txt { font-size: 17px; line-height: 20px; color: #313131; font-weight: bold; padding-left: 20px; flex: 1 1 auto; width: 1%; }

.user_list_p li { padding-bottom: 12px; }

.user_list_p span, .user_list_p strong { font-size: 13px; line-height: 14px; }

.user_list_p span a, .user_list_p strong a { color: #1678d3; }

.user_list_p span { color: #7a7a7a; flex: 1 1 auto; width: 1%; padding-right: 10px; }

.user_list_p strong { color: #313131; font-weight: normal; flex: 0 0 110px; max-width: 110px; }

.gray_bg { background: #ececec; border: 3px solid #e5e5e5; border-radius: 5px; }

.gray_bg p { font-size: 15px; line-height: 22px; color: #464646; }

.verification_block h3 { margin: 0; font-weight: bold; color: #313131; font-size: 17px; line-height: 20px; text-align: center; }

.verification_block h4 { margin: 0; font-weight: 500; color: #cd0146; font-size: 15px; line-height: 20px; text-align: center; }

.verification_block .circle { width: 120px; height: 120px; flex: 0 0 120px; max-width: 120px; margin: 10px auto; border-radius: 50%; background: #cd0146; }

.verification_block .circle i { font-size: 75px; color: #fff; }

.verification_block .col_120 { flex: 0 0 160px; max-width: 160px; }

.verification_block .inputfile + label { height: 40px; background: #cac8c8; padding-top: 10px; }

.verification_block .py_18 { padding-top: 18px; padding-bottom: 18px; }

.verification_ok .circle { background: #28a745; }

.verification_ok h4 { color: #28a745; }
